import { Component, ViewEncapsulation, OnInit, Inject } from '@angular/core';
import { LocalStorageService } from 'ngx-webstorage';
import { UtilService } from '@core/services/utils.service';
import { UserSession } from '@core/models/user-session';
// import { InventoryService } from '@app/modules/Inventory/services/inventory.service';
import { ToastrService } from 'ngx-toastr';
import { MatDatepicker } from '@angular/material/datepicker';
import { DatePipe } from '@angular/common';
import { InventoryApiService, MOMENT } from '@core/services';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from '@angular/forms';
import { GridOptions, GridApi, ColumnApi } from 'ag-grid/main';
import { UserService } from '@core/services/user.service';
@Component({
  selector: 'app-workorder',
  templateUrl: './workorder.component.html',
  styleUrls: ['./workorder.component.scss'],
  encapsulation: ViewEncapsulation.Emulated
})
export class WorkOrderComponent implements OnInit {
  private sessionData: UserSession;
  private gridApi: GridApi;
  private gridColumnApi: ColumnApi;
  workOrderForm: FormGroup;
  assetTypesName: any[];
  employeeList: any[];
  assetData: {};
  selectedRows: any[];
  editable = false;

  columnDefs = [
    { headerName: 'Asset Name', field: 'assetNm', checkboxSelection: true },
    { headerName: 'Work Order No', field: 'workorderNo' },
    {
      headerName: 'Work Order Date',
      field: 'workorderDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
    },
    { headerName: 'Work Order Approval', field: 'approverNm' },
    { headerName: 'Work Duration', field: 'workDuration' },
    { headerName: 'Assigned Person', field: 'assignedUserNm' },
    {
      headerName: 'Planned StartDt',
      field: 'startDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
    },
    {
      headerName: 'Planned EndDt',
      field: 'endDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
    },
    { headerName: 'workPriority', field: 'workPriority' },
    { headerName: 'Approval Remarks', field: 'approverRemarks' },
    { headerName: 'Maintainance Tasks', field: 'maintenanceTasks' }
  ];

  rowData: any;
  constructor(
    @Inject(MOMENT) private moment,
    private utilService: UtilService,
    private inventoryService: InventoryApiService,
    private toastr: ToastrService,
    private datePipe: DatePipe,
    private fb: FormBuilder,
    private userService: UserService
  ) {
    this.checkLocalStorageValue();
  }

  ngOnInit() {
    this.fetchAssetTypesData();
    this.fetchEmployeeList();
    this.initForm();
    this.selectedRows = [];
  }

  /**
   * @description open the form for work order
   * @function addWorkOrder
   */
  addWorkOrder() {
    this.editable = true;
    this.workOrderForm.reset();
  }
  /**
   * @description comparing two objects for get the droopdown value while editing
   * @param o1 {object}
   * @param o2 {object}
   */
  assertTypeComparator(o1, o2) {
    console.log(o1, o2);
    if (o2) {
      return o1.assetId === o2.assetId;
    }
  }
  /**
   * @description going to fetch page from form page
   * @function backToScreen
   */
  backToScreen() {
    this.editable = false;
    this.selectedRows = [];
  }

  agGridDateFormatter(params) {
    return this.moment(params.value).format('DD/MM/YYYY');
  }
  /**
   * @description get the localStorage value
   * @function checkLocalStorageValue
   */
  checkLocalStorageValue() {
    this.sessionData = this.userService.getUser();
  }
  /**
   * @description select the date from date picker
   * @param datePicker
   * @function datePickerTogler
   */
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  /**
   * @description edit the asset work order
   * @function editAsset
   */
  editAssest() {
    const assetId = {
      assetId: this.selectedRows[0].assetId,
      assetNm: this.selectedRows[0].assetNm,
      plannedMaintenanceId: this.selectedRows[0].plannedMaintenanceId
    };
    const obj = this.selectedRows[0];
    obj.assetId = assetId;
    this.workOrderForm.patchValue(obj);
    this.editable = true;
  }
  /**
   * @description fetch the AssetTypes
   * @function fetchAssetTypesData
   */
  fetchAssetTypesData() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.inventoryService.fetchPlannedMaintenanceAssetNames(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.assetTypesName = response.data;
        } else if (response.response === 'E400') {
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description fetch the assetWOrkOrder information
   * @param payload {onject}
   * @function fetchAssetWorkOrderInfo
   */
  fetchAssetsWorkOrderInfo(payload) {
    this.inventoryService.fetchAssetsWorkOrderInfo(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          // this.rowData = response.data;
          this.gridApi.setRowData(response.data);
          // this.toastr.success('Success!', response.responseMessage);
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description fetch employee list within organization
   * @function fetchEmployeeList
   */
  fetchEmployeeList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.inventoryService.fetchEmployeeList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.employeeList = response.data;
        } else if (response.response === 'E400') {
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  /**
   * @description calling methods in onload
   * @function initForm
   */
  initForm() {
    this.workOrderForm = this.fb.group({
      assetId: this.fb.control('', Validators.required),
      workorderDt: this.fb.control('', Validators.required),
      workorderApprover: this.fb.control('', Validators.required),
      workDuration: this.fb.control('', Validators.required),
      assignUsrId: this.fb.control('', Validators.required),
      startDt: this.fb.control(''),
      endDt: this.fb.control(''),
      approverRemarks: this.fb.control(''),
      maintenanceTasks: this.fb.control(''),
      workorderNo: this.fb.control(''),
      workPriority: this.fb.control('')
    });
  }
  /**
   * grid trigger when component loads
   * @param params {}
   * @function onGridReady
   */
  onGridReady(params) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.fetchAssetsWorkOrderInfo(payload);
  }
  /**
   * @description get the all selected rows from grid
   * @function onSelectionChanged
   */
  onSelectionChanged() {
    this.selectedRows = this.gridApi.getSelectedRows();
  }
  /**
   * @description get the data of selected Asset
   * @function selectAssetType
   */
  selectAssetType() {
    this.assetData = this.workOrderForm.get('assetId').value;
  }
  /**
   * @description insert or update the workorder info
   * @param data {}
   */
  submitWorkOrderForm(data) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      ...this.workOrderForm.value,
      workorderDt: this.datePipe.transform(
        this.workOrderForm.value.workorderDt,
        'yyyy-MM-dd'
      ),
      startDt: this.datePipe.transform(
        this.workOrderForm.value.startDt,
        'yyyy-MM-dd'
      ),
      endDt: this.datePipe.transform(
        this.workOrderForm.value.endDt,
        'yyyy-MM-dd'
      )
    };
    if (this.assetData) {
      payload.assetId = this.assetData['assetId'];
      payload.plannedMaintenanceId = this.assetData['plannedMaintenanceId'];
    } else {
      payload.assetId = this.selectedRows[0].assetId.assetId;
      payload.plannedMaintenanceId = this.selectedRows[0].assetId.plannedMaintenanceId;
    }

    if (this.selectedRows.length) {
      payload.workorderId = this.selectedRows[0].workorderId;
    }
    this.inventoryService.insertOrUpdateAssetsWorkOrderInfo(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.editable = false;
          this.workOrderForm.reset();
          this.selectedRows = [];
          // this.toastr.success('Success!', response.responseMessage);
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description open the form for filling the data of work order
   * @function updateWorkOrder
   */
  updateWorkOder() {
    this.editable = true;
  }
}
