import { Component, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { AgRendererComponent } from 'ag-grid-angular';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { InventoryComponent } from './../../containers/inventory/inventory.component';
import { UtilService } from '@core/services/utils.service';
import { LocalStorageService } from 'ngx-webstorage';
import { UserSession } from '@core/models/user-session';
import { InventoryShiftModalComponent } from './../../containers/assetLocation/inventory.shiftmodal.component';
import { ICellRendererParams } from 'ag-grid/dist/lib/rendering/cellRenderers/iCellRenderer';
import { RowNode } from 'ag-grid/dist/lib/entities/rowNode';
import { GridApi } from 'ag-grid/dist/lib/gridApi';
import { OnChanges, DoCheck } from '@angular/core/src/metadata/lifecycle_hooks';
import { UserService } from '@core/services/user.service';
@Component({
  selector: 'app-asset-location',
  templateUrl: './assetLocationCell.component.html',
  styleUrls: ['./assetLocationCell.component.scss'],
  encapsulation: ViewEncapsulation.Emulated
})
export class AssetLocationCellComponent implements AgRendererComponent {
  public headOffice: boolean;
  private rowNode: RowNode;
  private api: GridApi;

  constructor(
    private cdref: ChangeDetectorRef,
    public dialog: MatDialog,
    private utilService: UtilService,
    private storage: LocalStorageService,
    private userService: UserService
  ) {
    this.checkLocalStorageValue();
  }
  private selectData = <any>{};
  sessionData: UserSession;

  agInit(params: ICellRendererParams) {
    this.api = params.api;
    this.rowNode = params.node;
    this.selectData = params.data;
    this.headOffice = this.sessionData.orgInfo.isHeadOffice;
  }

  /**
   * @description getting value from localStorage
   * @function checkLocalStorageValue
   */
  checkLocalStorageValue() {
    this.sessionData = this.userService.getUser();
  }

  refresh() {
    return false;
  }
  /**
   * opening modal for shifting the asset to another sub branches
   */
  modalOpen() {
    const dialogRef = this.dialog.open(InventoryShiftModalComponent, {
      data: { getData: this.selectData },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      if (result) {
        this.selectData.isShifted = true;
        this.api.refreshCells({ force: true });
      }
    });
  }
}
