

import { Routes, RouterModule, Router } from '@angular/router';
import { NgModule } from '@angular/core';
import { InventoryComponent } from './containers/inventory/inventory.component';
import { AssetLocationComponent } from './containers/assetLocation/assetLocation.component';
import { WorkOrderComponent } from './containers/WorkOrder/workorder.component';
//import { AssetPartsComponent } from './containers/AssetParts/assetPart.component';
const routes: Routes = [
  { path: 'asset-store', component: InventoryComponent },
  { path: 'asset-location', component: AssetLocationComponent },
  { path: 'work-order', component: WorkOrderComponent },
  //{ path: 'asset-part', component: AssetPartsComponent },
  { path: '', redirectTo: 'asset-store', pathMatch: 'prefix' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  declarations: []
})
export class InventoryRouterModule {}
