import { Component, OnInit, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { UserSession } from '@core/models/user-session';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { GridOptions, GridApi, ColumnApi } from 'ag-grid/main';
import { MatDatepicker } from '@angular/material/datepicker';
import { DatePipe } from '@angular/common';
import {
  Router,
  ActivatedRouteSnapshot,
  ActivatedRoute
} from '@angular/router';
import { MatRadioModule } from '@angular/material/radio';
import { InventoryModalComponent } from './inventory.modal.component';
import { InventoryShiftModalComponent } from './../assetLocation/inventory.shiftmodal.component';
import { UtilService } from '@core/services/utils.service';
import { LocalStorageService } from 'ngx-webstorage';
import { InventoryApiService, MOMENT } from '@core/services';
import { AssetLocationCellComponent } from './../../components/AssetLocationChangeCell/assetLocationCell.component';
import { UserService } from '@core/services/user.service';
import { OpdApiService } from '@core/services';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
@Component({
  templateUrl: './inventory.component.html',
  styleUrls: ['./../../inventory.component.scss']
})
export class InventoryComponent implements OnInit {
  AssestForm: FormGroup;
  defaultFetch = true;
  editData = false;
  maintainanceType: string;
  assetData: {};
  noOfDays: any;
  private gridApi: GridApi;
  private gridColumnApi: ColumnApi;
  public selectedRows: any[];
  public departmentList: any[];
  public employeeList: any[];
  public assetTypesName: any[];
  // private datePipe: DatePipe;
  private getDeptObj: {};
  private getEmployeeObj: {};
  private assetTypeNm: string;
  private headOffice: boolean;
  private sessionData: UserSession;
  columnDefs = [
    { headerName: 'Asset Name', field: 'assetNm', checkboxSelection: true },
    { headerName: 'Make', field: 'make' },
    { headerName: 'Modal', field: 'model' },
    { headerName: 'Manufacturer Name', field: 'manufacturerNm' },
    { headerName: 'Serial Number', field: 'serialNo' },
    { headerName: 'Cost Price', field: 'costPrice' },
    { headerName: 'Location', field: 'assetLocation' },
    {
      headerName: 'Purchase Date',
      field: 'purchageDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
    },
    {
      headerName: 'Last Maintenance Date',
      field: 'lastMaintenanceDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
    },
    {
      headerName: 'Next Maintenance Date',
      field: 'nextMaintenanceDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
    },
    { headerName: 'Last Maintenance Reading', field: 'lastMintenanceReading' },
    { headerName: 'Next Maintenance Reading', field: 'nextMaintenanceReading' },
    {
      headerName: 'Action',
      filed: 'actions',
      cellStyle: function (params) {
        return { display: 'flex', alignItems: 'center' };
      },
      cellRendererFramework: AssetLocationCellComponent
    }
  ];

  rowData: any;

  constructor(
    private storage: LocalStorageService,
    private http: HttpClient,
    @Inject(MOMENT) private moment,
    private fb: FormBuilder,
    private inventoryService: InventoryApiService,
    private toastr: ToastrService,
    private router: Router,
    private datePipe: DatePipe,
    private activatedRoute: ActivatedRoute,
    public dialog: MatDialog,
    private utilService: UtilService,
    public userService: UserService,
    private opdApiService: OpdApiService
  ) {
    this.checkLocalStorageValue();
  }

  ngOnInit() {
    this.initForm();
    this.selectedRows = [];
    this.employeeList = [];
    // this.fetchDepartmentList();
    this.fetchAssetTypesData();
    this.getRoutingParamsData();
    this.maintainanceType = 'maintainanceDate';
    this.headOffice = this.sessionData.orgInfo.isHeadOffice;
  }

  initForm() {
    this.AssestForm = this.fb.group({
      assetNm: this.fb.control('', Validators.required),
      make: this.fb.control(''),
      model: this.fb.control(''),
      serialNo: this.fb.control(''),
      manufacturerNm: this.fb.control(''),
      purchageDt: this.fb.control(''),
      costPrice: this.fb.control(''),
      deptId: this.fb.control('', Validators.required),
      hourPrice: this.fb.control(''),
      lastMaintenanceDt: this.fb.control(''),
      nextMaintenanceDt: this.fb.control(''),
      lastMintenanceReading: this.fb.control(''),
      nextMaintenanceReading: this.fb.control(''),
      responsibleUsrId: this.fb.control('', Validators.required),
      shortCode: this.fb.control('', Validators.required),
      assetTypeId: this.fb.control('', Validators.required),
      assetLocation: this.fb.control(''),
      durationDays: this.fb.control('')
    });
  }

  /**
   * @description opening dialog for adding assetType Name
   * @function addAssetType()
   */
  addAssetType() {
    const dialogRef = this.dialog.open(InventoryModalComponent, {
      // height: '350px'
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      this.assetTypeNm = result;
      if (result) {
        this.fetchAssetTypesData();
      }
    });
  }

  /**
   * @description opening form for adding Asset
   * @function addAsset()
   */
  addAssest() {
    this.editData = false;
    this.defaultFetch = false;
  }

  /**
   * @description comparing two objects for update AssetType
   * @function asserrTypeComparator
   * @param {object, object}
   */
  assertTypeComparator(o1, o2) {
    return o1 && o2 && o1.assetTypeId === o2.assetTypeId;
  }

  employeeTypeComparator(e1, e2) {
    return e1.responsibleUsrId === e2.responsibleUsrId;
    // console.log(e1);
    // console.log(e2);
  }

  agGridDateFormatter(params) {
    console.log(params);
    if (params.value) {
      return this.moment(params.value).format('DD/MM/YYYY');
    }
  }

  /**
   * @description back to fecth page from adding asset page
   * @function backToScreen
   */
  backToScreen() {
    this.defaultFetch = true;
    this.selectedRows = [];
    //this.AssestForm.get('assetLocation').value = '';
    this.AssestForm.reset();
  }

  /**
   * @description getting value from localStorage
   * @function checkLocalStorageValue
   */
  checkLocalStorageValue() {
    this.sessionData = this.userService.getUser();
  }
  /**
   * @description open date picker toggle
   * @param {MatDatepicker<Date>} datePicker
   * @function datePickerToggler
   */
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  /**
   * @description select department from DrpDown to get the selected object
   * @function departmentSelect
   */
  departmentSelect() {
    this.getDeptObj = this.AssestForm.get('deptId').value;
    this.fetchEmployeeList(this.getDeptObj);
  }
  /**
   * @description select employee from DrpDown to get the selected object
   * @function employeeSelect
   */
  employeeSelect() {
    this.getEmployeeObj = this.AssestForm.get('responsibleUsrId').value;
  }
  /**
   * @description edit the asset
   * @function editAsset
   */
  editAssest() {
    const asset = {
      assetTypeId: this.selectedRows[0].assetTypeId,
      assetTypeNm: this.selectedRows[0].assetTypeNm,
      deptId: this.selectedRows[0].deptId,
      responsibleUsrId: this.selectedRows[0].responsibleUsrId,
      empNm: this.selectedRows[0].empNm,
      shortCode: this.selectedRows[0].shortCode
    };
    const obj = this.selectedRows[0];
    obj.assetTypeId = asset;
    this.fetchEmployeeList(this.selectedRows[0].deptId, empList => {
      this.employeeList = empList;
      this.noOfDays = this.selectedRows[0].maintenanceDuration;
      this.AssestForm.patchValue({
        responsibleUsrId: this.selectedRows[0].responsibleUsrId
      });
    });

    this.AssestForm.patchValue(obj);
    this.defaultFetch = false;
    this.editData = true;
  }

  deleteAsset() {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Inventory',
        data: 'Reason for Deleting Inventory'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          deleteReason: result,
          deleteUsrId: this.sessionData.regId,
          plannedMaintenanceId: this.selectedRows[0].plannedMaintenanceId,
          assetId: this.selectedRows[0].assetId
        };
        this.inventoryService.deleteAssetInformation(deletePayload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              console.log(this.rowData);
              const selected = this.gridApi.getSelectedRows();
              const res = this.gridApi.updateRowData({ remove: selected });
              this.removeSelectedGridData(res);
              this.toastr.success('Success!', response.responseMessage);
            } else if (response.response === 'E400') {
              this.toastr.error('Failure!', response.responseMessage);
            }
          },
          err => {
            this.toastr.error('Failure!', 'Something went wrong');
          }
        );
      }
    });
  }

  removeSelectedGridData(res) {
    if (res.remove) {
      res.remove.forEach(function (rowNode) {
        console.log('Removed Row Node', rowNode);
      });
    }
  }
  /**
   * @description fetch the AssetTypeNames
   * @function fetchAssetTypesData
   */
  fetchAssetTypesData() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.inventoryService.fetchAssetTypes(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.assetTypesName = response.data;
        } else if (response.response === 'E400') {
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   *@description fetch assets within the organization
   * @param payload
   * @function fetchAssetData
   */
  fetchAssetData(payload) {
    this.inventoryService.fetchAsset(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          // this.rowData = response.data;
          console.log(response.data);
          this.gridApi.setRowData(response.data);
          // this.toastr.success('Success!', response.responseMessage);
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description fetch Department lIst
   * @function fetchDepartmentList
   */
  // fetchDepartmentList() {
  //   const payload = {
  //     orgId: this.sessionData.orgId,
  //     orgGroupId: this.sessionData.orgGroupId,
  //     createdUsrId: this.sessionData.regId
  //   };
  //   this.opdApiService.fetchDepartmentList(payload).subscribe(
  //     (response: any) => {
  //       if (response.responseCode === 'E200') {
  //         this.departmentList = response.data;
  //       } else if (response.response === 'E400') {
  //       }
  //     },
  //     err => {
  //       this.toastr.error('Failure!', 'Something went wrong');
  //     }
  //   );
  // }

  /**
   * @description fetch employee list based on Department Id
   * @param id
   * @function fetchEmployeeList
   */
  fetchEmployeeList(id, cb?) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      deptId: id
    };
    this.inventoryService.fetchEmployeeList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.employeeList = response.data;
          if (typeof cb === 'function') {
            cb(response.data);
          }
        } else if (response.response === 'E400') {
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  getRoutingParamsData() {
    this.activatedRoute.queryParams.subscribe(params => {
      if (params['isAdd']) {
        this.defaultFetch = false;
      }
    });
  }
  /**
   * @description get the selected rows from grid
   * @function onSelectionChanged
   */
  onSelectionChanged() {
    this.selectedRows = this.gridApi.getSelectedRows();
  }
  /**
   * @description calling the grid onload when the compoment is triggered
   * @param params
   * @function onGridReady
   */
  onGridReady(params) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    // this.rowData =  params.rowData,
    this.fetchAssetData(payload);
  }
  /**
   * @description get the assetTypeId object from dropDown
   * @function selectAssetType
   */
  selectAssetType() {
    this.assetData = this.AssestForm.get('assetTypeId').value;
  }
  /**
   * @description here inserting and updating asset form
   * @param value
   * @function submitAssetForm
   */
  submitAssestForm(value) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      ...this.AssestForm.value,
      purchageDt: this.datePipe.transform(
        this.AssestForm.value.purchageDt,
        'yyyy-MM-dd'
      ),
      lastMaintenanceDt: this.datePipe.transform(
        this.AssestForm.value.lastMaintenanceDt,
        'yyyy-MM-dd'
      ),
      nextMaintenanceDt: this.datePipe.transform(
        this.AssestForm.value.nextMaintenanceDt,
        'yyyy-MM-dd'
      )
    };
    if (this.assetData) {
      payload.responsibleUsrId = this.getEmployeeObj;
      payload.assetTypeId = this.assetData['assetTypeId'];
      payload.assetTypeNm = this.assetData['assetTypeNm'];
    } else {
      payload.assetTypeId = this.selectedRows[0].assetTypeId.assetTypeId;
      payload.assetTypeNm = this.selectedRows[0].assetTypeId.assetTypeNm;
      payload.responsibleUsrId = this.selectedRows[0].assetTypeId.responsibleUsrId;
    }
    //
    if (this.editData === true) {
      payload.assetId = this.selectedRows[0].assetId;
      payload.plannedMaintenanceId = this.selectedRows[0].plannedMaintenanceId;
    }
    if (this.noOfDays) {
      payload.maintenanceDuration = this.noOfDays;
    } else {
      payload.lastMaintenanceDt = '';
      payload.nextMaintenanceDt = '';
    }

    console.log(payload);
    // return;
    this.inventoryService.insertAssest<boolean>(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.defaultFetch = true;
          this.AssestForm.reset();
          this.selectedRows = [];
          //this.toastr.success('Success!', response.responseMessage);
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  submitDays() {
    this.AssestForm.patchValue({
      lastMaintenanceDt: this.datePipe.transform(new Date(), 'yyyy-MM-dd')
    });
    const date = this.moment()
      .add(this.noOfDays, 'd')
      .toDate();
    this.AssestForm.patchValue({
      nextMaintenanceDt: this.datePipe.transform(date, 'yyyy-MM-dd')
    });
  }
}
