import { Component, OnInit, Inject } from '@angular/core';
import { GridOptions, GridApi, ColumnApi } from 'ag-grid/main';
import { UserSession } from '@core/models/user-session';
import {
  Router,
  ActivatedRouteSnapshot,
  ActivatedRoute
} from '@angular/router';
// import { InventoryService } from './../../services/inventory.service';
import { ToastrService } from 'ngx-toastr';
import { UtilService } from '@core/services/utils.service';
import { InventoryShiftModalComponent } from './../assetLocation/inventory.shiftmodal.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { InventoryApiService, MOMENT } from '@core/services';
import { UserService } from '@core/services/user.service';
@Component({
  selector: 'app-assetloc',
  templateUrl: './assetLocatoin.component.html',
  styleUrls: ['./assetLocation.component.scss']
})
export class AssetLocationComponent implements OnInit {
  private sessionData: UserSession;
  private gridApi: GridApi;
  private gridColumnApi: ColumnApi;
  private selectedRows: any[];
  public headOffice: {};
  columnDefs = [
    { headerName: 'Asset Name', field: 'assetNm', checkboxSelection: true },
    { headerName: 'Shifted Location', field: 'orgNm' },
    { headerName: 'Address', field: 'addressLine1' },
    {
      headerName: 'Shifted Date',
      field: 'startDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
    },
    { headerName: 'Transport HR', field: 'transportHr' },
    { headerName: 'Remarks', field: 'remarks' }
  ];

  rowData: any;

  constructor(
    @Inject(MOMENT) private moment,
    private activatedRoute: ActivatedRoute,
    private toastr: ToastrService,
    private router: Router,
    private inventoryService: InventoryApiService,
    private utilService: UtilService,
    public dialog: MatDialog,
    private userService: UserService
  ) {
    this.checkLocalStorageValue();
  }
  ngOnInit() {
    this.selectedRows = [];
    this.headOffice = this.sessionData.orgInfo.isHeadOffice;
  }
  /**
   * @description get the localstorae value
   * @function checkLocalStorageValue
   */
  checkLocalStorageValue() {
    this.sessionData = this.userService.getUser();
  }
  agGridDateFormatter(params) {
    return this.moment(params.value).format('DD/MM/YYYY');
  }
  /**
   * @description opening the modal for editing the shift location
   */
  editAssestLocation() {
    const obj = {
      stringCheck: 'editAssetLocation',
      ...this.selectedRows[0]
    };
    const dialogRef = this.dialog.open(InventoryShiftModalComponent, {
      data: { getData: obj },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 'success') {
        const payload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId
        };
        this.selectedRows = [];
        this.fetchShiftLocationData(payload);
      }
    });
  }

  /**
   * @description fetch assets Locations
   * @param payload { object }
   */
  fetchShiftLocationData(payload) {
    this.inventoryService.fetchShiftLocationData(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.gridApi.setRowData(response.data);
          // this.toastr.success('Success!', response.responseMessage);
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  /**
   * @description grid triigger on onload
   * @param params {object}
   */
  onGridReady(params) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.fetchShiftLocationData(payload);
  }

  /**
   * @description get the seleted rows from grid
   * @function onSelectionChanged
   */
  onSelectionChanged($event) {
    this.selectedRows = this.gridApi.getSelectedRows();
  }
}
