import {
  NgModule,
  NO_ERRORS_SCHEMA,
  CUSTOM_ELEMENTS_SCHEMA
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { InventoryComponent } from './containers/inventory/inventory.component';
import { InventoryRouterModule } from './inventory-routing.module';
import { AgGridModule } from 'ag-grid-angular';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { api, INVENTORY_END_POINTS } from './inventory.api';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { InventoryModalComponent } from './containers/inventory/inventory.modal.component';
import { AssetLocationComponent } from './containers/assetLocation/assetLocation.component';
import { InventoryShiftModalComponent } from './containers/assetLocation/inventory.shiftmodal.component';
import { AssetLocationCellComponent } from './components/AssetLocationChangeCell/assetLocationCell.component';
import { WorkOrderComponent } from './containers/WorkOrder/workorder.component';
import { MatRadioModule } from '@angular/material/radio';
import { MatDialogModule } from '@angular/material';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
@NgModule({
  declarations: [
    InventoryComponent,
    InventoryModalComponent,
    AssetLocationComponent,
    InventoryShiftModalComponent,
    AssetLocationCellComponent,
    WorkOrderComponent,
    // CommentsDialogComponent
    //AssetPartsComponent
  ],
  imports: [
    InventoryRouterModule,
    HttpClientModule,
    AgGridModule.withComponents([AssetLocationCellComponent]),
    FormsModule,
    CommonModule,
    ReactiveFormsModule,
    MatDatepickerModule,
    MatRadioModule,
    MatDialogModule
  ],
  exports: [],
  providers: [
    { provide: INVENTORY_END_POINTS, useValue: api }
  ],
  entryComponents: [
    InventoryModalComponent,
    InventoryShiftModalComponent,
    CommentsDialogComponent
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class InventoryModule {}
