import { Component, Inject, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
// import { InventoryService } from './../../services/inventory.service';
import { UserSession } from '@core/models/user-session';
import { ToastrService } from 'ngx-toastr';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DatePipe } from '@angular/common';
import { UserService } from '@core/services/user.service';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from '@angular/forms';
import { MatDatepicker } from '@angular/material/datepicker';
import {
  Router,
  ActivatedRouteSnapshot,
  ActivatedRoute
} from '@angular/router';
import { UtilService } from '@core/services/utils.service';
import {InventoryApiService} from '@core/services';
@Component({
  selector: 'app-shift-dialog',
  templateUrl: 'inventory.shiftmodal.component.html',
  styleUrls: ['./../../inventory.component.scss']
})
export class InventoryShiftModalComponent implements OnInit {
  assetType: string;
  orgList: any[];
  assetName: string;
  getSelectBranchObj: string;
  ShiftLocationForm: FormGroup;
  private sessionData: UserSession;
  constructor(
    private http: HttpClient,
    private fb: FormBuilder,
    private inventoryService: InventoryApiService,
    private activatedRoute: ActivatedRoute,
    private toastr: ToastrService,
    private datePipe: DatePipe,
    public dialogRef: MatDialogRef<InventoryShiftModalComponent>,
    private utilService: UtilService,
    private userService: UserService,
    @Inject(MAT_DIALOG_DATA) public selectedRowData: any
  ) {
    this.checkLocalStorageValue();
  }

  ngOnInit() {
    this.initForm();
    this.fetchSubBranchOrgNames();
    this.editAssetLocation();
  }
  /**
   * @description get the local storage value
   * @function checkLocalStorageValue
   */
  checkLocalStorageValue() {
    this.sessionData = this.userService.getUser();
  }
  /**
   * @description closing the modal
   * @function cancelForm
   */
  cancelForm() {
    this.dialogRef.close();
  }
  /**
   * @description open date picker
   * @param datePicker
   * @function datePickerToggler
   */
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  /**
   * @description editing the asset location
   * @function editAssetLocation
   */
  editAssetLocation() {
    const locationData = this.selectedRowData.getData;
    if (locationData.stringCheck) {
      this.ShiftLocationForm.patchValue(locationData);
    }
  }
  /**
   * @description fetch subbranches
   * @function fetchSubBranchesNames
   */
  fetchSubBranchOrgNames() {
    const payload = {
      orgId: this.sessionData.orgId,
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId
    };
    this.inventoryService.fetchSubBranchOrgNames(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.orgList = response.data;
        } else if (response.response === 'E400') {
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  /**
   * @description triggering the method on onload
   * @function initForm
   */
  initForm() {
    const getData = this.selectedRowData.getData;
    this.assetName = getData.assetNm;
    this.ShiftLocationForm = this.fb.group({
      assetNm: this.fb.control('', Validators.required),
      remarks: this.fb.control(''),
      orgId: this.fb.control('', Validators.required),
      transportHr: this.fb.control('', Validators.required)
    });
  }
  /**
   * @description select the sub branch from drop down
   * @function subBranchselect
   */
  subBranchSelect() {
    this.getSelectBranchObj = this.ShiftLocationForm.get('orgId').value;
  }
  /**
   * @description save the after filling the asset shift location
   * @function saveAssetShift
   */
  saveAssetShift() {
    const payload = {
      orgGrpId: this.sessionData.orgGroupId,

      createdUsrId: this.sessionData.regId,

      startDt: this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
      ...this.ShiftLocationForm.value
    };
    payload.assetId = this.selectedRowData.getData.assetId;
    if (this.getSelectBranchObj) {
      payload.toOrgId = this.getSelectBranchObj;
    } else {
      payload.toOrgId = this.selectedRowData.getData.orgId;
    }
    if (this.selectedRowData.getData.assetMovementId) {
      payload.assetMovementId = this.selectedRowData.getData.assetMovementId;
      payload.startDt = this.selectedRowData.getData.startDt;
    }
    payload.orgId = this.sessionData.orgId;
    this.inventoryService.InsertAssetLocation(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.dialogRef.close('success');
        } else if (response.response === 'E400') {
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
}
